.ImageGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  padding-top: 17px;
  padding-bottom: 17px;
}

.Warning {
  width: 500px;
  text-align: center;
  margin: 10px auto;
  background-color: #fff;
  color: #046d0c;
  font-weight: 900;
  font-size: 25px;
}
